
.App{
    background-color: rgb(30, 30, 30);    
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.sum {
    display:  flex;
    justify-content: space-between;
    align-items: center;
    background-color: black;
    width: 110%;
    font-family: 'Helvetica', 'Arial', sans-serif;
}
.logo{
    margin-left: 1rem;
    color: white;
    font-size: 2em;
    font-weight: bolder;
}
.ul {
    display: flex;
    margin: 2em;
}
.item{
    display: flex;
}
li{
    margin: 0 1rem;
}
a{
    text-decoration: none;
    color: white;
    font-weight: bolder;
}

@media only screen and (max-width:700px){
    .sum{
        display: flex;
        width: 700px;
    }
}
#roombutton{
    background-color:aqua;
    border: none;
    color: rgb(3, 3, 3);
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    border-radius: 0.3em;
}
h2{
    color: white;
    font-family: 'Helvetica', 'Arial', sans-serif;
    font-weight: bolder;
}
.accessibility{
    border: 0; 
    clip: rect(0 0 0 0); 
    height: 1px; 
    margin: -1px; 
    overflow: hidden; 
    padding: 0; 
    position: absolute; 
    width: 1px; 
}
.flex-container {
    display: flex;
    height: 70vh;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.flex-inner{
    display: flex;
    height: 10em;
    width: 25em;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    border-radius: 0.3em;
    margin: 8em;
    color: black;
}
.flex-rooms{
    display: flex;
    width: 110%;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    padding: 1em;
    border-radius: 0.3em;
    margin: 1rem;
}
.flex-room{
    display: flex;
    width: 96%;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    padding: 1em;
    border-radius: 0.3em;
    margin: 1rem;
    background-color: rgb(10, 10, 10);
}

@media only screen and (max-width:900px) and (min-width:700px){
    .flex-inner{
        display: flex;
        height: 1em;
    }
    .flex-room{
        display: flex;
        width: 80%;
    }
}

@media only screen and (max-width:700px){
    .flex-inner{
        display: flex;
        height: 1em;
        border-radius: 0.3em;
        margin: 4em;
    }
    .flex-rooms{
        padding: 1em;
        margin: 1rem;
    }
    .flex-room{
        display: flex;
        width: 300px;
        padding: 1em;
        margin: 0rem;
    }
}

.room-container {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

h1 {
    font-family: Arial, Helvetica, sans-serif;
    color: white;
    font-style: bold;
    margin: 1em;
}

.Room{
    background-color: rgb(30, 30, 30);    
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}
label{
    color:white;
    font-weight: bold;
    font-family: 'Helvetica', 'Arial', sans-serif;
    margin: 1em;
}

#UserName{
    color:white;
    background-color: rgb(30, 30, 30); 
}
.video-container {
    display: flex;
}

.video-input {
    display: flex;
    flex-direction: column;
    position: absolute;
    justify-content: flex-start;
    height: 20vh;
}

.video{
    margin-top: 10rem;
}

#Url{
    color:white;
    background-color: rgb(30, 30, 30); 
}

input[type=submit]{
    background: aqua;
    border: none;
    margin: 1em;
}

label{
    color:white;
    font-weight: bold;
    font-family: 'Helvetica', 'Arial', sans-serif;
    margin: 1em;
}
#background-video {
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
}

@media only screen and (max-width:700px){
  #background-video {
    visibility: hidden;
  }
}
.flex-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer-container {
    width: 100vw;
    height: 3em;
    position: absolute;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-content: space-around;
    color: white;
    background: black;
    bottom: 0px;
}

@media only screen and (max-width:900px) and (min-width:700px) {
    .footer-container {
        width: 80%;
        padding-left: 1em;
        padding-right: 1em;
        height: 4em;
        align-items: center;
        flex-direction: column;
    }
}

@media only screen and (max-width:700px) {
    .footer-container {
        width: 450px;
        padding-left: 1em;
        padding-right: 1em;
        height: 4em;
        align-items: center;
        flex-direction: column;
    }
}

p{
    margin:0px;
    height: 1em;
    font-family: 'Helvetica', 'Arial', sans-serif;
}
.element{
    text-align: center;
    display: inline-block;
    font-weight: bold;
    cursor: pointer;
}

.element:hover{
    color:aqua;
    transition: 50ms;
}

.roomlist_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: 'Helvetica', 'Arial', sans-serif;
}

.roomlist{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-auto-rows: 2rem;
    list-style: none;
}

@media only screen and (max-width:900px) and (min-width:700px){
    .roomlist{
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-auto-rows: 2.5rem;
    }
}

@media only screen and (max-width:700px){
    .roomlist{
        display: grid;
        grid-template-columns: 1fr;
        grid-auto-rows: 2rem;
    }
}
